<!--
@added | response from the server
-->
<template>
  <el-dialog title=" " :visible.sync="inDialog" :class="theme">
    <div>
      <p>
        <span>:</span>
        <el-input placeholder="" v-model="form.first_name" @blur="firstToUpper('first_name')" />
      </p>
      <p>
        <span>:</span>
        <el-input placeholder="" v-model="form.last_name" @blur="firstToUpper('last_name')" />
      </p>
      <p>
        <span>:</span>
        <el-input placeholder="" v-model="form.middle_name" @blur="firstToUpper('middle_name')" />
      </p>
      <p>
        <span> :</span>
        <el-input placeholder=" " v-model="form.academic_title" @blur="firstToUpper('academic_title')" />
      </p>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="inDialog = false"></el-button>
      <el-button type="primary" @click="fetchAdd()"></el-button>
    </span>
  </el-dialog>
</template>

<script>
import axios from 'axios'

export default {
  props: {
    dialog: {
      type: Boolean,
      required: true
    }
  },
  data () {
    return {
      form: {},
      inDialog: false,
      loading: false
    }
  },
  computed: {
    theme () {
      return this.$store.state.template.theme
    }
  },
  methods: {
    fetchAdd () {
      this.loading = true

      axios.post('teachers', this.form)
        .then(res => {
          this.$emit('added', res.data)
          this.$message({ type: 'success', message: '' })
          this.form = {}
          this.loading = false
          this.inDialog = false
        })
        .catch(() => {
          this.$message({ type: 'error', message: ' ' })
          this.loading = false
        })
    },
    firstToUpper (name) {
      let val = this.form[name]

      if (typeof val !== 'undefined') {
        val = val.trim()
        if (val) {
          this.form[name] = val[0].toUpperCase() + val.slice(1)
        }
      }
    }
  },
  watch: {
    dialog () {
      this.inDialog = true
    }
  }
}
</script>



