<!--
@added | response from the server
-->
<template>
  <el-dialog title=" " :visible.sync="inDialog" :class="theme">
    <div>
      <span>:</span>
      <el-input placeholder="" v-model="form.name" />
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="inDialog = false"></el-button>
      <el-button type="primary" @click="fetchAdd()"></el-button>
    </span>
  </el-dialog>
</template>

<script>
import axios from 'axios'

export default {
  props: {
    dialog: {
      type: Boolean,
      required: true
    },
    name: {
      type: String,
      required: false
    }
  },
  data () {
    return {
      form: {},
      inDialog: false,
      loading: false
    }
  },
  computed: {
    theme () {
      return this.$store.state.template.theme
    }
  },
  methods: {
    fetchAdd () {
      this.loading = true

      axios.post('subjects', this.form)
        .then(res => {
          this.$emit('added', res.data)
          this.$message({ type: 'success', message: '' })
          this.form = {}
          this.loading = false
          this.inDialog = false
        })
        .catch(() => {
          this.$message({ type: 'error', message: ' ' })
          this.loading = false
        })
    }
  },
  watch: {
    dialog () {
      this.inDialog = true
      this.form.name = this.name
    }
  }
}
</script>



