<!--
@deleted | index
@edited | response from the server | index
-->
<template>
  <el-dialog :title="item.name" :visible.sync="inDialog">
    <div>
      <span> :</span>
      <el-input placeholder="" v-model="inItem.name" />
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button type="danger" class="delete" icon="el-icon-delete" @click="openDelete()" size="small"
                 :loading="loading" :disabled="loading" />
      <el-button @click="inDialog = false"></el-button>
      <el-button type="primary" @click="fetchEdit()" :loading="loading" :disabled="loading">
        
      </el-button>
    </span>
  </el-dialog>
</template>

<script>
import axios from 'axios'

export default {
  props: {
    dialog: {
      type: Boolean,
      required: true
    },
    item: {
      type: Object,
      required: true
    },
    index: {
      type: Number,
      required: false
    }
  },
  data () {
    return {
      inItem: {},
      inDialog: false,
      loading: false
    }
  },
  methods: {
    fetchEdit () {
      this.loading = true

      axios.put('subjects/' + this.inItem.id, this.inItem)
        .then(res => {
          this.$emit('edited', res.data, this.index)
          this.$message({ type: 'success', message: '' })
          this.loading = false
          this.inDialog = false
        })
        .catch(() => {
          this.$message({ type: 'error', message: ' ' })
          this.loading = false
        })
    },
    fetchDelete () {
      this.loading = true

      axios.delete('subjects/' + this.inItem.id)
        .then(() => {
          this.$emit('deleted', this.index)
          this.$message({ type: 'success', message: '' })
          this.loading = false
          this.inDialog = false
        })
        .catch(() => {
          this.$message({ type: 'error', message: ' ' })
          this.loading = false
        })
    },
    openDelete () {
      this.$confirm('     ?', this.item.name, {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.fetchDelete()
      }).catch(() => {})
    }
  },
  watch: {
    dialog () {
      this.inItem = Object.assign({}, this.item)
      this.inDialog = true
    }
  }
}
</script>

<style lang="scss" scoped>
.delete {
  float: left;
  height: 40px;
}
</style>



